html {
    box-sizing: border-box;
    overflow-y: scroll;
    scroll-behavior: smooth;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    font-family: $f-primary, sans-serif;
    font-size: 18px;
    line-height: 22px;
    color: $c-font-main;
    background-color: $c-bg-main;
}

svg {
    fill: $c-font-main;
}
